import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ArrowBackIos from '@material-ui/icons/ArrowBackIos'
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import CreditCard from '@material-ui/icons/CreditCard';
import CalendarToday from '@material-ui/icons/CalendarToday';
import Lock from '@material-ui/icons/Lock';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import axios from 'axios'


const styles = theme => ({
    margin: {
      margin: theme.spacing.unit,
    },
    card: {
        width:400,
        marginTop:100,
        marginLeft:'auto',
        marginRight:'auto'
    },

    mcard: {
        width:'100%',
        marginTop:50,
        marginLeft:'auto',
        marginRight:'auto'
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    bbutton:{
        float:'left',
        marginTop:10,
        marginLeft:10
    },
    title: {
        fontSize: '1em',
        marginLeft:'15px',
        color:'black',
        textAlign:'left'
    },
    mtitle: {
        fontSize: '1em',
        color:'black',
        textAlign:'left'
    },
    leftth:{
        color:'black',
        fontWeight:'bold',
        fontSize:"1em"
    },
    rightth:{
        color:'red',
        fontWeight:'bold',
        fontSize:"1em"
    },
    pos: {
        marginBottom: 12,
    },
    bdiv:{
        width:'100%',
        float:'left',
        height:80,
    },
    input:{
        width:"350px",
        marginTop:10,
        marginLeft:10,
        marginBottom:10
    },

    minput:{
        width:"100%",
        marginTop:10,
        marginBottom:10
    },
        sinput:{
            width:"170px",
            marginLeft:10,
            marginTop:10,
            marginBottom:10
        },
    cardnum:{
        width:'100%',
    },
    button:{
        width:"100%"
    }
});
class Payment extends React.Component  {
    constructor(props) {
        super(props);
        this.state = {
            total:"$100",
            cardnum:"",
            date:"",
            cvc:""
        }
        
        this.pay = this.pay.bind(this)
        this.cnChange = this.cnChange.bind(this);
        this.cdChange = this.cdChange.bind(this);
        this.cvChange = this.cvChange.bind(this);
      }
      cnChange(event) {
        this.setState({
          cardnum  :event.target.value
         })
      }
      cdChange(event) {
        this.setState({
          date  :event.target.value
         })
      }
      cvChange(event) {
        this.setState({
         cvc  :event.target.value
        })
      }
 
  pay = async () =>{
    let cardnum = this.state.cardnum;
    let date = this.state.date;
    let cvc = this.state.cvc;
    if(cardnum===""){
        alert("Card Number Required!")
        return;
    }
    if(date===""){
        alert("Date Required!")
        return;
    }
    if(cvc===""){
        alert("CVC Number Required!")
        return;
    }


      var url = "/add_pay/"
    let res = await axios.get(url, {
        params: {
            amount: this.props.total,
            status: "Paid"
        }
    })
    if(res.status==="sucess"){
        this.props.showHome()
    }
    else{
        alert(res.status)
    }
        console.log(res)
        
}


  render() {
    const { classes } = this.props;

    let size = window.innerWidth;
    if (size > 1000) {
        return (
            <Card className={classes.card}>
            <div className={classes.bdiv}>
            <Button variant="contained" size="small" className={classes.bbutton} onClick={this.props.showHome}>
              <ArrowBackIos></ArrowBackIos>
               BACK
              </Button>
        
            </div>
              <CardContent>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                Card Details
                </Typography>
                <FormControl className={classes.input}>
                <Input  onChange={this.cnChange} 
                    placeholder="Card Number"
                  id="input-with-icon-adornment"
                  startAdornment={
                    <InputAdornment position="start">
                    <CreditCard></CreditCard>
                    </InputAdornment>
                  }
                />
              </FormControl>
              <TextField
                className={classes.sinput}
                placeholder="MM / YY"
                id="input-with-icon-textfield"
                onChange={this.cdChange} 
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                        <CalendarToday></CalendarToday>
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                className={classes.sinput}
                id="input-with-icon-textfield"
                placeholder="CVC"
                onChange={this.cvChange} 
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                    <Lock></Lock>
                    </InputAdornment>
                  ),
                }}
              />
                           <Table>
                        <TableBody>
                                <TableRow className={classes.leftth}>
                                <TableCell component="th" scope="row" className={classes.leftth}>
                                Billed Now 
                                </TableCell>
                                <TableCell className={classes.rightth} >$ {this.props.total}</TableCell>
                      </TableRow>
          </TableBody>
        </Table>
              
              </CardContent>
              <CardActions>
              <Button variant="contained" color="primary" className={classes.button} onClick={this.pay}>
                Buy Now
              </Button>
              </CardActions>
            </Card>
          );
        
    }else{
        return (
            <Card className={classes.mcard}>
            <div className={classes.bdiv}>
            <Button variant="contained" size="small" className={classes.bbutton} onClick={this.props.showHome}>
              <ArrowBackIos></ArrowBackIos>
               BACK
              </Button>
        
            </div>
              <CardContent>
                <Typography className={classes.mtitle} color="textSecondary" gutterBottom>
                Card Details
                </Typography>
                <FormControl className={classes.minput}>
                <Input  onChange={this.cnChange} 
                    placeholder="Card Number"
                  id="input-with-icon-adornment"
                  startAdornment={
                    <InputAdornment position="start">
                    <CreditCard></CreditCard>
                    </InputAdornment>
                  }
                />
              </FormControl>
              <TextField
                className={classes.minput}
                placeholder="MM / YY"
                id="input-with-icon-textfield"
                onChange={this.cdChange} 
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                        <CalendarToday></CalendarToday>
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                className={classes.minput}
                id="input-with-icon-textfield"
                placeholder="CVC"
                onChange={this.cvChange} 
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                    <Lock></Lock>
                    </InputAdornment>
                  ),
                }}
              />
                           <Table>
                        <TableBody>
                                <TableRow className={classes.leftth}>
                                <TableCell component="th" scope="row" className={classes.leftth}>
                                Billed Now 
                                </TableCell>
                                <TableCell className={classes.rightth} >$ {this.props.total}</TableCell>
                      </TableRow>
          </TableBody>
        </Table>
              
              </CardContent>
              <CardActions>
              <Button variant="contained" color="primary" className={classes.button} onClick={this.pay}>
                Buy Now
              </Button>
              </CardActions>
            </Card>
          );
                
    }
    }
}

Payment.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Payment);