import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Tab from './Tab'
import ListView from './List'
import Item from './Item'

import List from '@material-ui/core/List';
import axios from 'axios'

import Button from '@material-ui/core/Button';
import Product from './Product';
import Payment from './Payment'
const styles = theme => ({
    button: {
      position:'fixed',
      bottom:10,
      right:10,
    },
    extendedIcon: {
    },
    list:{
      marginBottom:100,
    },
  });
  
class Home extends React.Component {
  
    constructor(props) {
        super(props);
        this.state = {
           items:[],
           pitem:[],
           cart:[],
           view:true,
           cview:false,
           pview:false,
           payview:false,
           total:"",
        }
        this.getItems    = this.getItems.bind(this);
        this.payment = this.payment.bind(this)
      }
      
      componentDidMount() {
        this.getItems();
      }
      cartView =  () => {
        this.setState({
          view: false,
          cview:true,
          pview:false,
          payview:false,
        });
      };

      homeView =  () => {
        console.log("welcome")
        this.setState({
          view: true,
          cview:false,
          pview:false,
          payview:false,
        });
      };
      productView =  (item) => {
        this.setState({
          view: false,
          cview:false,
          pview:true,
          payview:false,
          pitem:item
        });
      };
      deletecart = async (id)=>{
        console.log(id)
        var url = "/delete_cart/"+id
        let {data} = await axios.get(url);
        if(data.status==="sucess"){
          this.getItems()
          alert("Item Removed")          
        }else{
        alert("Unable to Remove")          
        }
      };
      payment= ()=>{
        var num =0;
        var data = this.state.cart; 
        for(var i=0;i<data.length;i++){
          num = num + parseInt(data[i].price)
        }
        if(num!==0){
          this.setState({
            view: false,
            cview:false,
            pview:false,
            payview:true,
            total:num
          });
        }else{
          alert("Cart is Empty!")
        }
 
      }
      
      getItems = async () =>{
        let res = await axios.get("/product_view/")      
        this.setState({items:res.data}) 
        let {data} = await axios.get("/cart_view_data/")      
        this.setState({cart:data}) 
        
        }    
    render(){

        const { classes } = this.props;
      return(
          <div>
              <Tab showCart={this.cartView.bind(this)} showHome={this.homeView.bind(this)}></Tab>
              {this.state.cview ?(
                <div>
                <List className={classes.list}>
                  {
                    this.state.cart.map((item, i) => <ListView item={item} key={i} deletecart={this.deletecart.bind(this)}  />)

                }           
              </List>
              <Button variant="extendedFab" onClick={this.payment} color="secondary" className={classes.button}>CHECK OUT</Button>
              </div >
              ): this.state.view ?(             
                <div className={classes.list}>

                  {
                    this.state.items.map((item, i) => <Item item={item} key={i} getItemList={this.getItems.bind(this)} productView={this.productView.bind(this)} />)
                }           
    
                </div>
                   ):this.state.pview ?(
                     <div>
                       <Product showHome={this.homeView.bind(this)}  item={this.state.pitem}  />
                     </div>
                   ):this.state.payview ?(
                     <div>
                        <Payment showHome={this.homeView.bind(this)}item="name" total={this.state.total}></Payment>
                     </div>
                    ):(
                     <div></div>
                   )


              }
                </div>

      )

  }

}

Home.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Home);