import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import RemoveShoppingCart from '@material-ui/icons/RemoveShoppingCart';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  right:{
      paddingRight:20,
      color:'mediumseagreen'
  },
  remove:{
    color:'red'
}
});

class ListView extends React.Component {
  dcart = ()=>{
    this.props.deletecart(this.props.item.product_Id)
  }

  render() {

    let data = this.props.item
    const { classes } = this.props;
    return (
      <div className={classes.root}>
            <ListItem button>
              <Avatar alt="Remy Sharp" src={'/media/'+data.imageurl} />
              <ListItemText primary={data.Productname}   secondary={
                  <Typography type="body2" className={classes.right}>
                    $ {data.price} 
                </Typography>
              }/>
              <ListItemSecondaryAction onClick={this.dcart}>
                <IconButton aria-label="Comments">
                  <RemoveShoppingCart className={classes.remove}/>
                </IconButton>
              </ListItemSecondaryAction>
 
            </ListItem>
        <Divider />
      </div>
    );
  }
}

ListView.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ListView);