import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';



const styles = {
    card: {
        width: 1000,
        marginTop: 10,
        marginLeft: 'auto',
        marginRight: 'auto'
    },
    mcard: {
        width: "90%",
        marginLeft: "5%",
        marginTop: "5%",
    },
    position:{
        width: 1000,
        marginTop: 10,
        marginLeft: 'auto',
        marginRight: 'auto',
        height:40,
    },
    left: {
        width: 350,
        height: 450,
        float: 'left'
    },
    content: {
        width: 640,
        height: 450,
        marginTop: 20,
        float: 'left'
    },

    mcontent: {
        width:"100%",
        marginTop: 20,
        float: 'left'
    },
    media: {
        height: 350,
        width: 350,
        marginTop: 20,
        float: 'left',
        objectFit: "contain"
    },
    mmedia: {
        height: 300,
        marginTop: 10,
        width: "100%",
        objectFit: "contain"
    },
    title: {
        textAlign: 'left',
        marginBottom: 10
    },
    tabletop:{
        borderTop:'1px solid lightgrey'
    },
    stitle: {
        textAlign: 'left',
        marginBottom: 10,
        color: "grey"
    },
    mtitle: {
        textAlign: 'center',
        marginBottom: 10,
        color: "black"
    },
    smtitle: {
        textAlign: 'left',
        marginLeft: 10,
        color: "grey"
    },
    button: {
        width: 200,
        marginLeft: 800,
        float: 'right',
    },

    mbutton: {
        width: '100%',
        float: 'right',
    },
    lbutton: {
        width:100,
        float: 'left',
        background:'grey'
    },

    mlbutton: {
        width:100,
        marginLeft:'20px',
        float: 'left',
        background:'grey'
    },
    price: {
        color: 'mediumseagreen',
        textAlign: 'left'
    },

    mprice: {
        color: 'mediumseagreen',
        textAlign: 'center'
    },
    rightth:{
        background:"#f0f0f0",
        fontSize:"1em"
    },
    leftth:{
        fontSize:"1em"
    }
};

class Product extends React.Component {

    addtocart = (id, qty) => async () => {
        var url = "/cart_view/"
        let res = await axios.get(url, {
            params: {
                product_Id: id,
                quantity: qty
            }
        })
        if (res.data.auth === "fail") {
            window.location = "/login/"
        } else {
            alert("Added to Cart")
            this.props.getItemList()
        }

    }
    render() {
        const { classes } = this.props;
        let data = {
        /*    Productname: "TO KILL A MOCKINGBIRD",
            category: "Classic",
            colour: "null",
            imageurl: "media/download.jpg",
            price: "20",
            product_Id: "001",
            stock: "5",
            unit: 'null' 
        */
       }
        if (this.props.item != null) {
              data = this.props.item
        }
        let size = window.innerWidth;
        if (size > 1000) {
            return (
                <div >
                    <div className={classes.position}>
                    <Button variant="contained"  color="primary" className={classes.lbutton } onClick={ this.props.showHome }>
                        <ArrowBackIos className={classes.leftIcon}  />
                        Back
                  </Button>
                    </div>
                    <Card className={classes.card}>

                        <div className={classes.left} >
                            <img
                                className={classes.media}
                                           src={'/media/'+data.imageurl} alt="No"/>
                                 
                        </div>
                        <div className={classes.content}>
                            <Typography variant="title" noWrap className={classes.title}  >
                                {data.Productname || ''}
                            </Typography>
                            <Typography gutterBottom variant="h3" component="h2" className={classes.price}>
                                $ {data.price || ""}
                            </Typography>
                            <Typography variant="h6" noWrap className={classes.stitle}  >
                                Specifications
                            </Typography>
                <Table>
                <TableBody>
                {
                    data.category != null &&
                    <TableRow className={classes.tabletop}>
                    <TableCell component="th" scope="row" className={classes.leftth}>
                    Category
                    </TableCell>
                    <TableCell className={classes.rightth} >{data.category||""}</TableCell>
                  </TableRow>
                    }
              {
                    data.colour != null &&
                        <TableRow className={classes.tabletop}>
                        <TableCell component="th" scope="row" className={classes.leftth}>
                        Colour
                        </TableCell>
                        <TableCell className={classes.rightth} >{data.colour}</TableCell>
              </TableRow>
                                
                }

              {
                    data.stock != null &&
                        <TableRow className={classes.tabletop}>
                        <TableCell component="th" scope="row" className={classes.leftth}>
                        Stock
                        </TableCell>
                        <TableCell className={classes.rightth} >{data.stock}</TableCell>
              </TableRow>
                                
                }

              {
                    data.unit != null &&
                        <TableRow className={classes.tabletop}>
                        <TableCell component="th" scope="row" className={classes.leftth}>
                        Unit
                        </TableCell>
                        <TableCell className={classes.rightth} >{data.unit}</TableCell>
              </TableRow>
                                
                }

              </TableBody>
                </Table>
                        </div>

                        <CardActions>
                            <Button variant="contained" color="secondary" className={classes.button} onClick={this.addtocart(data.product_Id, 1)}>
                                ADD TO CART
</Button>
                        </CardActions>
                    </Card>
                </div>

            )
        } else {
            return (
                <div >
                <div className={classes.position}>
                <Button variant="contained"  color="primary" className={classes.mlbutton } onClick={ this.props.showHome }>
                    <ArrowBackIos className={classes.leftIcon}  />
                    Back
              </Button>
                </div>
                <Card className={classes.mcard}>
                    <img className={classes.mmedia}
                                       src={'/media/media/download.jpg'} alt="No"/>
                             
                    <div className={classes.mcontent}>
                        <Typography variant="title" noWrap className={classes.mtitle}  >
                            {data.Productname || 'Product Name'}
                        </Typography>
                        <Typography gutterBottom variant="h3" component="h2" className={classes.mprice}>
                            $ {data.price || "100"}
                        </Typography>
                        <Typography variant="h6" noWrap className={classes.smtitle}  >
                            Specifications
                        </Typography>
            <Table>
            <TableBody>
            {
                data.category != null &&
                <TableRow className={classes.tabletop}>
                <TableCell component="th" scope="row" className={classes.leftth}>
                Category
                </TableCell>
                <TableCell className={classes.rightth} >{data.category||""}</TableCell>
              </TableRow>
            }
          {
                data.colour != null &&
                    <TableRow className={classes.tabletop}>
                    <TableCell component="th" scope="row" className={classes.leftth}>
                    Colour
                    </TableCell>
                    <TableCell className={classes.rightth} >{data.colour}</TableCell>
          </TableRow>
                            
            }

          {
                data.stock != null &&
                    <TableRow className={classes.tabletop}>
                    <TableCell component="th" scope="row" className={classes.leftth}>
                    Stock
                    </TableCell>
                    <TableCell className={classes.rightth} >{data.stock}</TableCell>
          </TableRow>
                            
            }

          {
                data.unit != null &&
                    <TableRow className={classes.tabletop}>
                    <TableCell component="th" scope="row" className={classes.leftth}>
                    Unit
                    </TableCell>
                    <TableCell className={classes.rightth} >{data.unit}</TableCell>
          </TableRow>
                            
            }

          </TableBody>
            </Table>
                    </div>

                    <CardActions>
                        <Button variant="contained" color="secondary" className={classes.mbutton} onClick={this.addtocart(data.product_Id, 1)}>
                            ADD TO CART
</Button>
                    </CardActions>
                </Card>
            </div>
            )

        }
    }

}

Product.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Product);